<template>
  <div ref="wrapper" class="better-scroll-root">
    <!--UI-->
    <div class="content-bg better-scroll-container">
      <!---->
      <div v-if="pulldown" class="pulldown-tip">
        <i class="pull-icon indexicon icon-pull-down" :class="[pulldownTip.rotate]"></i>
        <span class="tip-content">{{pulldownTip.text}}</span>
      </div>
      <div v-show="loadingStatus.showIcon || loadingStatus.status" class="loading-pos">
        <div v-show="loadingStatus.showIcon" class="loading-container">
          <div class="cube">
            <div class="side side1"></div>
            <div class="side side2"></div>
            <div class="side side3"></div>
            <div class="side side4"></div>
            <div class="side side5"></div>
            <div class="side side6"></div>
          </div>
        </div>
        <span class="loading-connecting">{{loadingStatus.status}}</span>
      </div>
      <slot></slot>
    </div>
  </div>
</template>
<script>
import BScroll from "better-scroll";

export default {
  props: {
    /**
     * 1 scroll
     * 2 scroll
     * 3 scrollswipescroll
     */
    probeType: {
      type: Number,
      default: 1
    },
    /**
     * click
     */
    click: {
      type: Boolean,
      default: true
    },
    /**
     * 
     */
    scrollX: {
      type: Boolean,
      default: false
    },
    /**
     * 
     */
    listenScroll: {
      type: Boolean,
      default: false
    },
    /**
     * 
     */
    data: {
      type: Array,
      default: null
    },
    /**
     * 
     */
    pullup: {
      type: Boolean,
      default: false
    },
    /**
     * 
     */
    pulldown: {
      type: Boolean,
      default: false
    },
    /**
     * 
     */
    beforeScroll: {
      type: Boolean,
      default: false
    },
    /**
     * scroll
     */
    refreshDelay: {
      type: Number,
      default: 20
    },
    /**
     * loadingloading
     * isShow: false
     * showIcon: false,    // loadingicon
     * status: ''  // '...', '', '', ''
     */
    loadingStatus: {
      type: Object,
      default: function() {
        return {
          showIcon: false,
          status: ""
        };
      }
    },
    /**
     * 
     */
    pulldownUI: {
      type: Boolean,
      default: true
    },
    /**
     * 
     */
    pullupUI: {
      type: Boolean,
      default: true
    },
    /**
     *   
     */
    scrollToEndFlag: false,
    scrollbar: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      Timer: null,
      loadingConnecting: false,
      pulldownTip: {
        text: "", // 
        rotate: "" // icon-rotate
      }
    };
  },
  mounted() {
    // DOMbetter-scroll
    setTimeout(() => {
      this._initScroll();
    }, 20);
  },
  methods: {
    _initScroll() {
      if (!this.$refs.wrapper) {
        return;
      }
      // better-scroll
      this.scroll = new BScroll(this.$refs.wrapper, {
        probeType: this.probeType,
        click: this.click,
        scrollX: this.scrollX,
        scrollbar: {
          fade: false,
          interactive: false // 1.8.0 
        }
      });

      // 
      if (this.listenScroll || this.pulldown || this.pullup) {
        let me = this;
        this.scroll.on("scroll", pos => {
          if (this.listenScroll) {
            me.$emit("scroll", pos);
          }

          if (this.pulldown) {
            // 
            if (pos.y > 50) {
              this.pulldownTip = {
                text: "",
                rotate: "icon-rotate"
              };
            } else {
              this.pulldownTip = {
                text: "", // 
                rotate: "" // icon-rotate
              };
            }
          }

          if (this.pullup) {
          }
        });
      }

      // 
      if (this.pullup) {
        this.scroll.on("scrollEnd", () => {
          // 
          if (this.scroll.y <= this.scroll.maxScrollY + 50) {
            this.$emit("scrollToEnd");
          }
        });
      }

      // 
      if (this.pulldown) {
        this.scroll.on("touchend", pos => {
          // 
          if (pos.y > 50) {
            setTimeout(() => {
              // 
              this.pulldownTip = {
                text: "", // 
                rotate: "" // icon-rotate
              };
            }, 600);
            this.$emit("pulldown");
          }
        });
      }

      // 
      if (this.beforeScroll) {
        this.scroll.on("beforeScrollStart", () => {
          this.$emit("beforeScroll");
        });
      }
      if (this.scrollToEndFlag) {
        this.scrollToEnd();
      }
    },
    disable() {
      // better-scrolldisable
      this.scroll && this.scroll.disable();
    },
    enable() {
      // better-scrollenable
      this.scroll && this.scroll.enable();
    },
    refresh() {
      console.log("refresh");
      // better-scrollrefresh
      this.scroll && this.scroll.refresh();
    },
    scrollTo() {
      // better-scrollscrollTo
      this.scroll && this.scroll.scrollTo.apply(this.scroll, arguments);
    },
    scrollToElement() {
      // better-scrollscrollToElement
      this.scroll && this.scroll.scrollToElement.apply(this.scroll, arguments);
    },
    //
    scrollToEnd() {
      clearTimeout(this.Timer);
      this.Timer = setTimeout(() => {
        this.scroll.scrollTo(0, this.scroll.maxScrollY);
      }, 100);
    }
  },
  watch: {
    // refreshDelayrefresh
    data() {
      setTimeout(() => {
        this.refresh();
        if (this.scrollToEndFlag) {
          this.scrollToEnd();
        }
      }, this.refreshDelay);
    }
  }
};
</script>
<style lang="stylus">
.bscroll-vertical-scrollbar {
  z-index: 1 !important;
  width: 0.029963rem !important;
  background: #2b292f !important;
}
.bscroll-indicator {
  background: #213e48 !important;
  border: none !important;
}
.better-scroll-root {
  height: 100%;
  position: relative;
  overflow: hidden;
  .loading-pos, .pulldown-tip {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 35px;
    color: #fcfcfc;
    text-align: center;
    z-index: 2000;
  }
  .loading-pos {
    background-color: rgba(7, 17, 27, 0.7);
  }
  .pulldown-tip {
    top: -50px;
    height: 50px;
    line-height: 50px;
    z-index: 1;
  }
  .pull-icon {
    position: absolute;
    top: 0;
    left: 30%;
    color: #a5a1a1;
    font-size: 1.5em;
    transition: all 0.15s ease-in-out;
  }
  .pull-icon.icon-rotate {
    transform: rotate(180deg);
  }
  .loading-container {
    position: absolute;
    height: 10px;
    width: 10px;
    left: 35%;
    top: 50%;
    transform: translate(-50%, -50%);
    perspective: 40px;
  }
  .loading-connecting {
    line-height: 35px;
  }
  .cube {
    height: 10px;
    width: 10px;
    transform-origin: 50% 50%;
    transform-style: preserve-3d;
    animation: rotate 3s infinite ease-in-out;
  }
  .side {
    position: absolute;
    height: 10px;
    width: 10px;
    border-radius: 50%;
  }
  .side1 {
    background: #4bc393;
    transform: translateZ(10px);
  }
  .side2 {
    background: #FF884D;
    transform: rotateY(90deg) translateZ(10px);
  }
  .side3 {
    background: #32526E;
    transform: rotateY(180deg) translateZ(10px);
  }
  .side4 {
    background: #c53fa3;
    transform: rotateY(-90deg) translateZ(10px);
  }
  .side5 {
    background: #FFCC5C;
    transform: rotateX(90deg) translateZ(10px);
  }
  .side6 {
    background: #FF6B57;
    transform: rotateX(-90deg) translateZ(10px);
  }
  @keyframes rotate {
    0% {
      transform: rotateX(0deg) rotateY(0deg);
    }
    50% {
      transform: rotateX(360deg) rotateY(0deg);
    }
    100% {
      transform: rotateX(360deg) rotateY(360deg);
    }
  }
}
</style>