<template>
  <comModal ref="comm" :styls="styls" :titleName="pageTitle">
    <div class="wrapper">
      <div class="PassWordAlert" v-if="showPsdAlert">
        <div class="moneyPsdInput">
          <Inp
            label=""
            placeholder=""
            name="psd"
            :Val="psd"
            type="password"
            @Change="InpChange"
            :labelWidth="4"
          ></Inp>
        </div>

        <div class="emailCode" v-if="userSafedata.isEmailVerify === 0">
          <Inp
            label=""
            placeholder=""
            name="emailCode"
            :Val="emailCode"
            :btnText="btnText"
            :btnDisabled="btnDisabled"
            type="text"
            @Change="InpChange"
            @sendCode="sendCode(2)"
            :labelWidth="4"
          ></Inp>
        </div>

        <Btn class="submit" text="" @click.native="getClientToken()"/>
      </div>

      <div class="TiXianAlertDiv" v-else>
        <div class="YuE">
          <div class="localMoney">
            <span></span>
            <span class="PKCount" v-if="data">{{scientificToNumber(data.availableBalance)}}PK</span>
          </div>
          <div class="localMoney iceMoney">
            <span></span>
            <span class="PKCount icePK" v-if="data">{{scientificToNumber(data.frozenBalance)}}PK</span>
          </div>
        </div>

        <ul class="modal-cont-top">
          <li
            v-for="item in coinList"
            :key="item.text"
            :class="check === item.text ? 'active' : 'unUse'"
            @click="ChooseCoin(item.type)"
          >
            <img
              class="logo"
              :src="check === item.text?item.checkPic:item.unChekPic"
              :alt="item.text"
            >
            <p>{{item.text}}</p>
            <img
              v-if="check === item.text"
              class="Jiao"
              src="../../../assets/Images/modal/icon_tixian_select_rb@3x.png"
              alt
            >
          </li>
        </ul>

        <div class="TiXianCount" v-if="data">
          <div class="tixianCount">
            <span class="countTitle"></span>
            <input
              :placeholder="`${scientificToNumber(data.availableBalance)}PK`"
              v-model="CountInput"
              type="text"
              @input="CountInputValueChange(CountInput)"
              @focus="onFocus(CountInput)"
              class="CountInput"
            >
            <span class="allButton" @click="AllMoney"></span>
            <span
              class="CoinRate"
              v-if="CountInput"
            >{{check=='USDT'?(CountInput / this.convertRatio).toFixed(0):(CountInput / this.convertRatio).toFixed(3)}}{{check}}</span>
          </div>
          <div class="tixianCount tixianAdress">
            <span class="countTitle"></span>
            <input
              :placeholder="`${check}`"
              :value="AdressInput"
              type="text"
              @input="AdressInputValueChange($event.target.value)"
              @focus="onFocus(AdressInput)"
              class="adreessInput"
            >
          </div>

          <div class="tixianTip">
            <p>*{{serviceCharge+check}},{{data.withdrawLimit + check}}{{data.dayWithdrawCount}}</p>
          </div>
        </div>

        <div class="submitDiv" v-if="data">
          <p class="tixian_cancel">
            <button @click="Close"></button>
          </p>
          <p class="tixian_submit">
            <button @click="showPassWordAlert"></button>
          </p>
        </div>
      </div>
    </div>
  </comModal>
</template>

<script>
import comModal from "components/Modal/comModal";
import Inp from "components/Modal/loginReg/Inp";
import Btn from "components/Modal/loginReg/Btn";
import ETH_select from "Images/modal/alert_eth_selected@3x.png";
import ETH_unselect from "Images/modal/alert_eth_unselect@3x.png";
import BTC_select from "Images/modal/alert_btc_selected@3x.png";
import BTC_unselect from "Images/modal/alert_btc_unselect@3x.png";
import USDT_select from "Images/modal/icon_coin_usdt_selected@3x.png";
import USDT_unselect from "Images/modal/icon_coin_usdt_unselect@3x.png";
import unChangeSelectImg from "Images/modal/icon_tixian_select_rb@3x.png";

export default {
  data() {
    return {
      styls: "",
      pageTitle: "",
      data: {},
      showPsdAlert: false,
      userSafedata: "",
      CountInput: "",
      AdressInput: "",
      psd: "",
      emailCode: "",
      btnText: "",
      btnDisabled: false,
      convertRatio: 10000, //PK
      serviceCharge: 0, //
      coinList: [
        {
          checkPic: ETH_select,
          unChekPic: ETH_unselect,
          text: "ETH",
          type: 1
        },
        {
          checkPic: BTC_select,
          unChekPic: BTC_unselect,
          text: "BTC",
          type: 2
        },
        {
          checkPic: USDT_select,
          unChekPic: USDT_unselect,
          text: "USDT",
          type: 3
        }
      ],
      check: "USDT",
      clientType: "", //
      md5Token: "", //
      isApp: false //app
    };
  },
  components: { comModal, Inp,Btn },
  mounted() {},
  watch: {
    showPsdAlert: function(show) {
      if (this.showPsdAlert) {
        this.styls = { height: "55%", width: "75%" };
      } else {
        this.styls = { height: "75%", width: "75%" };
      }
    }
  },
  methods: {
    // token  type:  1: 2. 3.
    getClientToken() {
      this.md5Token = "";
      if (this.isApp) {
        this.clientType = 0;
        this.$requestClientToken("");
      } else {
        this.clientType = 1;
        this.getGoogleToken();
      }
    },

    setToken(resToken) {
      this.md5Token = resToken;
      this.TiXianAction();
    },
    //
    async getGoogleToken() {
      console.log("token");
      const token = await this.$recaptchaToken();
      this.googleToken = token;
      console.log("token", this.googleToken);
      this.TiXianAction();
    },
    show() {
      this.styls = { height: "80%", width: "75%" };
      this.$refs.comm.show();
      this.UserSafeSetting();
      this.LoadWithDrawal();
      this.showPsdAlert = false;
      this.CountInput = "";
      this.AdressInput = "";
      this.psd = "";
      this.emailCode = "";
      this.check = "USDT";
      let str = this.$lStore.get("app_type");
      if (str) {
        if (str == "1") {
          this.isApp = true;
        } else {
          this.isApp = false;
        }
      } else {
        this.isApp = false;
      }
      window.setToken = this.setToken;
    },
    ChooseCoin(type) {
      if(type != 3){
        this.$toast('')
        return;
      }

      this.CountInput = "";
      this.AdressInput = "";
      this.psd = "";
      this.emailCode = "";

      console.log(this.coinList[type - 1].text);
      this.$clickAudio();
      
      this.check = this.coinList[type - 1].text;
      this.LoadWithDrawal();
    },
    Close() {
      this.$clickAudio();
      this.$refs.comm.close();
    },
    InpChange(val, name) {
      this[name] = val;
    },
    AdressInputValueChange(value) {
      this.AdressInput = value;
      this.$changeInput(this.AdressInput);
      console.log(this.AdressInput);
    },
    CountInputValueChange(value) {
      var val = value;
      val = val.replace(/[^\d.]/g, ""); //.
      val = val.replace(/\.{2,}/g, "."); //. 
      val = val
        .replace(".", "$#$")
        .replace(/\./g, "")
        .replace("$#$", ".");
      val = val.replace(/^(\-)*(\d+)\.(\d\d).*$/, "$1$2.$3"); //
      if (val.indexOf(".") < 0 && val != "") {
        // 0102
        val = parseFloat(val);
      }
      if (val <= this.scientificToNumber(this.data.availableBalance)) {
        this.CountInput = val;
      } else {
        this.CountInput = this.scientificToNumber(this.data.availableBalance);
      }
      this.$changeInput(this.CountInput);
      console.log(this.CountInput);
    },
    onFocus(val){
      this.$changeInput(val);
    },
    AllMoney() {
      this.$clickAudio();
      this.CountInput = this.scientificToNumber(this.data.availableBalance);
    },
    showPassWordAlert() {
      this.$clickAudio();
      if (this.CountInput && this.AdressInput) {
        this.showPsdAlert = true;
      } else if (!this.CountInput) {
        this.$toast("");
      } else if (!this.AdressInput) {
        this.$toast("");
      }
    },
    //
    sendCode(type) {
      this.$clickAudio();
      this.$ajax(
        {
          url: "/uac/user/sms",
          data: {
            params: { account: this.userSafedata.mobile, type: type || 2 }
          }
        },
        true
      )
        .then(res => {
          let time = 60,
            timer = null;
          this.btnDisabled = true;
          this.btnText = "60s";

          timer = setInterval(() => {
            --time;
            if (time === 0) {
              clearInterval(timer);
              this.btnDisabled = false;
              this.btnText = "";
            } else {
              this.btnText = time + "s";
            }
          }, 1000);
          this.$toast("");
        })
        .catch(err => {
          clearInterval(timer);
          this.btnDisabled = false;
          this.btnText = "";
          console.log(err);
        });
    },
    /**/
    scientificToNumber(num) {
      num = (num - 0).toLocaleString(); //"1,545,646,435,185,460,000,000,000,000"
      num = num.toString().replace(/\$|\,/g, "");
      return num;
    },
    //()
    UserSafeSetting() {
      this.$ajax(
        {
          url: "/uac/user/selectUserSetting",
          data: {
            params: {
              token: this.$lStore.get("Token")
            }
          }
        },
        true
      )
        .then(res => {
          if (res.code === 0) {
            console.log(res.data);
            this.userSafedata = res.data;
          }
        })
        .catch(err => {
          console.log(err);
        });
    },

    LoadWithDrawal() {
      this.data = null;
      var CoinType;
      for (let i = 0; i < this.coinList.length; i++) {
        if (this.coinList[i].text === this.check) {
          CoinType = this.coinList[i].type;
          break;
        }
      }
      var userHeader =
        CoinType === 1 ? "/ethes" : CoinType === 2 ? "/btces" : "/usdtes";
      console.log("ID", CoinType);
      this.$ajax(
        {
          url: userHeader + "/query/withdraw",
          data: {
            params: {
              token: this.$lStore.get("Token"),
              currencyType: CoinType
            }
          }
        },
        true
      )
        .then(res => {
          console.log(res);
          if (res.code === 0) {
            console.log(res.data);
            this.data = res.data;
            this.convertRatio = res.data.convertRatio; //PK
            console.log("", this.convertRatio);
            this.serviceCharge = res.data.serviceCharge; //
          }
        })
        .catch(err => {
          console.log(err);
        });
    },

    //
    TiXianAction() {
      this.$clickAudio();
      if (!this.psd) {
        this.$toast("");
        return;
      }

      var CoinType;
      for (let i = 0; i < this.coinList.length; i++) {
        if (this.coinList[i].text === this.check) {
          CoinType = this.coinList[i].type;
          break;
        }
      }
      var userHeader =
        CoinType === 1 ? "/ethes" : CoinType === 2 ? "/btces" : "/usdtes";
      this.$ajax(
        {
          url: userHeader + "/exchange/withdraw/apply",
          data: {
            params: {
              token: this.$lStore.get("Token"),
              currencyType: CoinType,
              address: this.AdressInput,
              amount: this.CountInput,
              pwd: this.psd,
              captcha: this.emailCode,
              recaptchaCode: this.googleToken,
              recaptchaVersion: this.clientType,
              md5Str: this.md5Token
            }
          }
        },
        true
      )
        .then(res => {
          console.log(res);
          if (res.code === 0) {
            console.log(res.data);
            this.$toast("");
            this.Close();
          }
        })
        .catch(err => {
          console.log(err);
        });
    }
  }
};
</script>

