//typeof7
function dataType(obj) {
  if (obj === null) {
    return "null";
  }
  if (obj !== obj) {
    return "nan";
  }
  if (typeof Array.isArray === "function") {
    if (Array.isArray(obj)) {
      //isArray()
      return "array";
    }
  } else {
    //toString
    if (Object.prototype.toString.call(obj) === "[object Array]") {
      return "array";
    }
  }
  return (typeof obj).toLowerCase();
}
var Store = function() {
  this.name = "Store";
};
Store.prototype = {
  init: function(options) {
    this.store = function() {
      return options.type;
    };
    return this;
  },
  set: function(key, value) {
    var type = dataType(value);

    switch (type) {
      case "object":
      case "array":
        this.store().setItem(key, JSON.stringify(value));
        break;
      // case 'array':
      //             this.store().setItem(key,'['+value+']');
      //             break;
      case "function": //eval()js
        this.store().setItem(key, value);
        break;
      default:
        this.store().setItem(key, value);
    }
  },
  get: function(key) {
    var value = this.store().getItem(key);

    try {
      value = JSON.parse(value);
    } catch (e) {}
    return value;
  },
  getAll: function() {
    var json = {};
    var value = "";

    for (var attr in this.store()) {
      try {
        value = JSON.parse(this.store()[attr]);
      } catch (e) {}
      json[attr] = value;
    }
    return json;
  },
  remove: function(key) {
    this.store().removeItem(key);
  },
  clear: function() {
    this.store().clear();
  }
};
const lStore = new Store().init({
  type: window.localStorage
});
const sStore = new Store().init({
  type: window.sessionStorage
});

//
function normalDate(oDate) {
  var CurrentDate = oDate;
  var reg = /\-+/g;

  if (dataType(oDate) == "string") {
    oDate = oDate.split(".")[0]; //ieyyyy-MM-dd HH:mm:ss.S
    oDate = oDate.replace(reg, "/"); //yyyy-MM-dd
  }

  CurrentDate = new Date(oDate);
  return CurrentDate;
}
//
//oDate
//fmt
//(M)(d)(h)(m)(s)(q)  1-2 
//(y) 1-4 (S) 1 ( 1-3 )
//
//dateFormat0(new Date(),'yyyy-MM-dd hh:mm:ss.S') ==> 2006-07-02 08:09:04.423
//dateFormat0(new Date(),'yyyy-M-d h:m:s.S')      ==> 2006-7-2 8:9:4.18
function dateFormat(mydate, ft) {
  var fmt = ft || "yyyy/MM/dd hh:mm:ss",
    reg = /\d/g;
  if (reg.test(mydate)) {
    mydate = mydate - 0;
  }
  var oDate = normalDate(mydate);
  var date = {
    "M+": oDate.getMonth() + 1, //
    "d+": oDate.getDate(), //
    "h+": oDate.getHours(), //
    "m+": oDate.getMinutes(), //
    "s+": oDate.getSeconds(), //
    "q+": Math.floor((oDate.getMonth() + 3) / 3), //+3
    S: oDate.getMilliseconds() //
  };

  if (/(y+)/.test(fmt)) {
    //RegExp.$1(99)
    fmt = fmt.replace(
      RegExp.$1,
      (oDate.getFullYear() + "").substr(4 - RegExp.$1.length)
    );
  }

  for (var attr in date) {
    if (new RegExp("(" + attr + ")").test(fmt)) {
      fmt = fmt.replace(
        RegExp.$1,
        RegExp.$1.length == 1
          ? date[attr]
          : ("00" + date[attr]).substring((date[attr] + "").length)
      );
    }
  }
  return fmt;
}
//
//$begin_time  
//$end_time 
function timediff($end_time) {
  var date1 = new Date(); //
  var date3 = $end_time - new Date().getTime(); //

  //
  var days = Math.floor(date3 / (24 * 3600 * 1000));

  //

  var leave1 = date3 % (24 * 3600 * 1000); //
  var hours =
    Math.floor(leave1 / (3600 * 1000)) < 10
      ? "0" + Math.floor(leave1 / (3600 * 1000))
      : Math.floor(leave1 / (3600 * 1000));
  //
  var leave2 = leave1 % (3600 * 1000); //
  var minutes =
    Math.floor(leave2 / (60 * 1000)) < 10
      ? "0" + Math.floor(leave2 / (60 * 1000))
      : Math.floor(leave2 / (60 * 1000));

  //
  var leave3 = leave2 % (60 * 1000); //
  var seconds =
    Math.round(leave3 / 1000) < 10
      ? "0" + Math.round(leave3 / 1000)
      : Math.round(leave3 / 1000);

  var $res = days + " " + hours + ":" + minutes + ":" + seconds;
  return $res;
}
//()
//oDate
function dateFormat1(myDate) {
  var oDate = normalDate(myDate);

  if (+oDate >= +new Date()) {
    return "";
  }
  var lookTime = +new Date() - +oDate;
  var seconds = Math.floor(lookTime / 1000);
  var minutes = Math.floor(lookTime / (1000 * 60));
  var hours = Math.floor(lookTime / (1000 * 60 * 60));
  var days = Math.floor(lookTime / (1000 * 60 * 60 * 24));
  var months = Math.floor(lookTime / (1000 * 60 * 60 * 24 * 30));
  var years = Math.floor(lookTime / (1000 * 60 * 60 * 24 * 30 * 12));

  if (seconds < 60) {
    lookTime = seconds + "";
  } else if (minutes < 60) {
    lookTime = minutes + "";
  } else if (hours < 24) {
    lookTime = hours + "";
  } else if (days < 30) {
    lookTime = days + "";
  } else if (months < 12) {
    lookTime = months + "";
  } else {
    lookTime = years + "";
  }
  return lookTime;
}

