function formatSecondsToHM(value) {
  var secondTime = parseInt(value); // 
  var minuteTime = 0; // 
  var hourTime = 0; // 
  if (secondTime > 60) {
    //60
    //60
    minuteTime = parseInt(secondTime / 60);
    //
    secondTime = parseInt(secondTime % 60);
    //60
    if (minuteTime > 60) {
      //60
      hourTime = parseInt(minuteTime / 60);
      //60
      minuteTime = parseInt(minuteTime % 60);
    }
  }
  var result = (secondTime < 10 ? "0" : "") + parseInt(secondTime);

  if (minuteTime >= 0) {
    result = (minuteTime < 10 ? "0" : "") + parseInt(minuteTime) + ":" + result;
  }
  if (hourTime >= 0) {
    result = (hourTime < 10 ? "0" : "") + parseInt(hourTime) + ":" + result;
  }
  return result;
}

function restOtherChip(num) {
  let arr = [];
  if (Math.floor(num / 5000)) {
    let len = Math.floor(num / 5000);
    for (let i = 0; i < len; i++) {
      arr = [...arr, 5000];
    }
    num = num % 5000;
  }
  if (Math.floor(num / 1000)) {
    let len = Math.floor(num / 1000);
    for (let i = 0; i < len; i++) {
      arr = [...arr, 1000];
    }
    num = num % 1000;
  }
  if (Math.floor(num / 500)) {
    let len = Math.floor(num / 500);
    for (let i = 0; i < len; i++) {
      arr = [...arr, 500];
    }
    num = num % 500;
  }
  // if (Math.floor(num / 200)) {
  //   let len = Math.floor(num / 200);
  //   for (let i = 0; i < len; i++) {
  //     arr = [...arr, 200];
  //   }
  //   num = num % 200;
  // }
  if (Math.floor(num / 100)) {
    let len = Math.floor(num / 100);
    for (let i = 0; i < len; i++) {
      arr = [...arr, 100];
    }
    num = num % 100;
  }
  if (Math.floor(num / 50)) {
    let len = Math.floor(num / 50);
    for (let i = 0; i < len; i++) {
      arr = [...arr, 50];
    }
    num = num % 50;
  }
  if (Math.floor(num / 10)) {
    let len = Math.floor(num / 10);
    for (let i = 0; i < len; i++) {
      arr = [...arr, 10];
    }
  }
  return arr;
}

/*
 * 
 * number
 * decimals
 * dec_point
 * thousands_sep
 * */
function numberFormat(number, decimals, dec_point, thousands_sep) {
  number = (number + "").replace(/[^0-9+-Ee.]/g, "");
  var n = !isFinite(+number) ? 0 : +number,
    prec = !isFinite(+decimals) ? 0 : Math.abs(decimals),
    sep = typeof thousands_sep === "undefined" ? "," : thousands_sep,
    dec = typeof dec_point === "undefined" ? "." : dec_point,
    s = "",
    toFixedFix = function(n, prec) {
      var k = Math.pow(10, prec);
      return "" + Math.ceil(n * k) / k;
    };

  s = (prec ? toFixedFix(n, prec) : "" + Math.round(n)).split(".");
  var re = /(-?\d+)(\d{3})/;
  while (re.test(s[0])) {
    s[0] = s[0].replace(re, "$1" + sep + "$2");
  }

  if ((s[1] || "").length < prec) {
    s[1] = s[1] || "";
    s[1] += new Array(prec - s[1].length + 1).join("0");
  }
  return s.join(dec);
}

function unitConvert(num) {
  var moneyUnits = ["", "", "", ""];
  var dividend = 10000;
  var curentNum = num; //
  var curentUnit = moneyUnits[0]; //
  for (var i = 0; i < 4; i++) {
    curentUnit = moneyUnits[i];
    if (strNumSize(curentNum) < 5) {
      break;
    }
    curentNum = curentNum / dividend;
  }
  var m = { num: 0, unit: "" };
  m.num = curentNum.toFixed(0);
  m.unit = curentUnit;
  return m.num + m.unit;
}
function strNumSize(tempNum) {
  var stringNum = tempNum.toString();
  var index = stringNum.indexOf(".");
  var newNum = stringNum;
  if (index != -1) {
    newNum = stringNum.substring(0, index);
  }
  return newNum.length;
}

